pre.highlight {
  box-sizing: border-box;
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
  padding: 1.25rem 20vw;

  @include tablet {
    position: static;
    width: 100%;
    padding: 1.25rem 0;
    margin: 0;
  }

  code {
    margin: 0 auto;
    display: block;
    width: 40rem;
    overflow-x: auto; 

    @include tablet {
      width: 100%;
    }
  }
}

code.highlighter-rouge {
  background: #e9ecef;
  color: #000; 
}